<script>
import { Bar } from 'vue-chartjs'
export default {
  extends: Bar,
  props: ['chartData', 'options'],
  display: true,
  fontFamily:'Vazir1',
  defaults:{
    font:{
      family:'Vazir1'
    }
  },
  mounted() {
    this.renderChart(this.chartData, this.options)
  }
}
</script>
