<template>
  <v-container>
    <p class="red--text text--darken-2" style="font-size:xxx-large">بررسی پیش بینی های مالی کوتاه مدت کسب وکار</p>
    <v-data-table
        v-if="loaded"
        :headers="headers"
        :items="mydata"
        item-key="name"
        class="elevation-1 alexis mr-10 "
        :search="search"
        hide-default-footer
    >
      <template v-slot:item="{item}">
        <tr v-if="item.title==='مالیات'" class="red">
          <td><b style="color: white">مالیات</b></td>
          <td colspan="6">
            <v-chip color="white">{{ maliat }}%</v-chip>
          </td>
        </tr>
        <tr v-else>
          <td v-for="(col, columnIndex) in headers" style="border: 1px solid black">
            <div v-if="columnIndex > 0">
              <v-chip color="white">{{ item[col.value] }}</v-chip>
            </div>
            <div v-else>
              <p style="margin-top: 10px">{{ item[col.value] }}</p>
            </div>
          </td>
        </tr>
      </template>
      <template v-slot:footer>
        <p class="grey--text my-0" style="font-size:small"><span><v-icon x-small color="red">mdi-star</v-icon></span>
          تمام اعداد گزارش شده برحسب میلیون ریال می باشد.
        </p>
        <p class="grey--text my-0" style="font-size:small">
          <span><v-icon x-small color="red">mdi-star</v-icon></span>
          <span><v-icon x-small color="red">mdi-star</v-icon></span>
          اختیاری
        </p>
      </template>
    </v-data-table>
    <v-row class="d-flex mr-10 mt-5">
      <v-col>
        <h2>وضعیت درآمدها و هزینه ها در کوتاه مدت</h2>
        <bar-chart v-if="loaded" :chartData="datacollection" :options="options"></bar-chart>
        <p class="grey--text my-0" style="font-size:small"><span><v-icon x-small color="red">mdi-star</v-icon></span>
          تمام اعداد گزارش شده برحسب میلیون ریال می باشد.
        </p>
      </v-col>
      <v-col>
        <h2>وضعیت سود خالص در کوتاه مدت</h2>
        <line-chart v-if="loaded" :chartData="myStaticData" :options="myOptions"></line-chart>
      </v-col>
    </v-row>
    <h2 class="mr-10 my-5">پارامترهای کلیدی در پیش بینی های مالی کوتاه مدت</h2>
    <v-row>
      <v-col xs="12" sm="6" md="3" class="d-flex flex-column">
        <v-progress-circular
            :rotate="180"
            :size="150"
            :width="10"
            :value="avg_hazineha"
            color="red"
            class="text-center"
        >
          <div class="flex flex-column">
            <span class="text--primary">{{ avg_hazineha }}</span>
            <hr width="100" style="height: 2px;background: black">
            <span class="font-weight-medium">درصد</span>
          </div>
        </v-progress-circular>
        <p class="arrow-down"></p>
        <span>میانگین رشد هزینه ها(کوتاه مدت)</span>
      </v-col>
      <v-col xs="12" sm="6" md="3" class="d-flex flex-column">
        <v-progress-circular
            :rotate="180"
            :size="150"
            :width="10"
            :value="avg_daramad"
            color="red"
            class="text-center"
        >
          <div class="flex flex-column">
            <span class="text--primary">{{ avg_daramad }}</span>
            <hr width="100" style="height: 2px;background: black">
            <span class="font-weight-medium">درصد</span>
          </div>
        </v-progress-circular>
        <p class="arrow-down"></p>
        <span>میانگین رشد درآمدها(کوتاه مدت)</span>
      </v-col>
      <v-col xs="12" sm="6" md="3" class="d-flex flex-column">
        <v-progress-circular
            :rotate="180"
            :size="150"
            :width="10"
            value="75"
            color="red"
            class="text-center"
        >
          <div class="flex flex-column">
            <span class="text--primary">{{ sarmaye }}</span>
            <hr width="100" style="height: 2px;background: black">
            <span class="font-weight-medium">میلیون ریال</span>
          </div>
        </v-progress-circular>
        <p class="arrow-down"></p>
        <span>سرمایه مورد نیاز(کوتاه مدت)</span>
      </v-col>
    </v-row>

  </v-container>
</template>
<script>
import axios from "axios";
import LineChart from "@/components/LineChart";
import BarChart from "@/components/BarChart";

export default {
  name: "Home",
  components: {BarChart, LineChart},
  data() {
    return {
      maliat: 0,
      avg_daramad: 0,
      avg_hazineha: 0,
      sarmaye: 0,
      interval: {},
      loaded: false,
      search: '',
      calories: '',
      mydata: [
        {title: 'درآمد'},
        {title: 'هزینه ها'},
        {title: 'سود ناخالص'},
        {title: 'مالیات'},
        {title: 'سود خالص'},
      ],
      headers: [
        {
          text: '',
          value: 'title',
          sortable: false,
          align: 'center'
        }, {
          text: 'فصل اول',
          value: 'fasl_1',
          sortable: false,
          class: "orange bold",
          align: 'center'
        },
        {
          text: 'فصل دوم',
          value: 'fasl_2',
          sortable: false,
          class: "deep-orange",
          align: 'center'
        },
        {
          text: 'فصل سوم',
          value: 'fasl_3',
          sortable: false,
          class: 'red',
          align: 'center'
        },
        {
          text: 'فصل چهارم',
          value: 'fasl_4',
          sortable: false,
          class: 'red darken-1',
          align: 'center'
        },
        {
          text: 'فصل پنجم*',
          value: 'fasl_5',
          sortable: false,
          class: 'red darken-2',
          align: 'center'
        },
        {
          text: 'فصل ششم**',
          value: 'fasl_6',
          sortable: false,
          class: 'red darken-3',
          align: 'center'
        },
      ],
      myOptions: {responsive: false, maintainAspectRatio: false, legend: false,scales: {
          yAxes: [{
            ticks: {
              beginAtZero: true,
              fontFamily: "Vazir1",
            },
            gridLines: {
              display: true
            }
          }],
          xAxes: [{
            ticks: {
              beginAtZero: true,
              fontFamily: "Vazir1",
            },
            gridLines: {
              display: false
            }
          }]
        },},
      myStaticData: {
        labels: ['فصل اول', 'فصل دوم', 'فصل سوم', 'فصل چهارم', 'فصل پنجم', 'فصل ششم'],
        datasets: [
          {
            label: 'سود خالص',
            backgroundColor: '#b00000',
            pointBackgroundColor: 'white',
            borderWidth: 1,
            pointBorderColor: '#52bf24',
            data: null,
            // data: [-350, -1500, 20110, 30100, 47000, 40000],
          },
        ]
      },
      datacollection: {
        labels: ['فصل اول', 'فصل دوم', 'فصل سوم', 'فصل چهارم', 'فصل پنجم', 'فصل ششم'],
        datasets: [
          {
            label: 'درآمد',
            backgroundColor: '#b00000',
            pointBackgroundColor: 'white',
            borderWidth: 1,
            pointBorderColor: '#52bf24',
            data: null,
          },
          {
            label: 'هزینه ها',
            backgroundColor: '#f80000',
            pointBackgroundColor: 'white',
            borderWidth: 1,
            pointBorderColor: '#bf2429',
            data: null,
          },
        ]
      },
      options: {
        scales: {
          yAxes: [{
            ticks: {
              beginAtZero: true,
              fontFamily: "Vazir1",
            },
            gridLines: {
              display: true
            }
          }],
          xAxes: [{
            ticks: {
              beginAtZero: true,
              fontFamily: "Vazir1",
            },
            gridLines: {
              display: false
            }
          }]
        },
        legend: {
          font: {
            family: 'Vazir1'
          },
          lables: {

          },
          display: true
        },
        tooltips: {
          enabled: true,
          mode: 'single',
          callbacks: {
            label: function (tooltipItems, data) {
              return tooltipItems.yLabel;
            }
          }
        },
        responsive: true,
        maintainAspectRatio: false,
        height: 200
      }
    }
  },
  async created() {
    this.loaded = false;
    try {
      const {data} = await axios.get('https://api.retiba.ir/vuejstest');
      this.datacollection.datasets[0].data = Object.values(data.daramad)
      this.datacollection.datasets[1].data = Object.values(data.hazineha)
      this.myStaticData.datasets[0].data = Object.values(data.sode_khales)
      this.avg_daramad = data.miangine_roshde_daramad;
      this.avg_hazineha = data.miangine_roshde_hazine;
      this.sarmaye = new Intl.NumberFormat().format(data.sarmaye_morede_niaz);
      this.maliat = data.maliat;
      Object.assign(this.mydata[0], data.daramad)
      Object.assign(this.mydata[1], data.hazineha)
      Object.assign(this.mydata[2], data.sode_nakhales)
      Object.assign(this.mydata[4], data.sode_khales)
      this.loaded = true
    } catch (e) {
      console.log(e)
    }
  },
}
</script>
<style scoped>
.arrow-down {
  position: relative;
  top: 0;
  right: 60px;
  width: 0;
  height: 0;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;
  border-top: 20px solid #ff7700;
}

.v-progress-circular {
  margin: 1rem;
}

::v-deep .v-data-table-header th {
  color: white !important;
}

::v-deep tbody tr:nth-child(1), ::v-deep tbody tr:nth-child(2) {
  background-color: #c7c7c4 !important;
}

::v-deep td {
  text-align: center !important;
  vertical-align: auto;
}
</style>
